import React, {Component} from "react";
import {OraFloatingLabelSelect} from "eshop/components/OraFloatingLabelSelect";
import ClientContextCheckbox from "eshop/modules/configurator/components/ClientContextCheckbox";
import InfoWithTooltip from "eshop/modules/configurator/components/InfoWithTooltip";
import PropTypes from "prop-types";
import MultiCartMnpBenefitComponent from "eshop/modules/cart/components/entriesList/mobile/MultiCartMnpBenefitComponent";
import OfferDisclaimerComponent from "eshop/modules/simfree/components/OfferDisclaimerComponent";
import OfferType from "eshop/modules/simfree/constants/OfferTypeEnum";
import {OraLoader} from "../../../../../components/common/OraLoader";
import DiscountInfo from "../../../../configurator/components/DiscountInfo";

const propTypes = {
    selectedProcessValue: PropTypes.string,
    processTypeData: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.string,
        description: PropTypes.string
    })),
    selectProcessCallback: PropTypes.func,
    selectedLoyaltyLengthValue: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    loyaltyLengthData: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.number
        ]),
        description: PropTypes.string
    })),
    selectLoyaltyLengthCallback: PropTypes.func,
    isB2B: PropTypes.bool,
    contractRole: PropTypes.string
}

export default class ProductDetailsProcessFilterView extends Component {

    createExtOfferSelect () {
        let selectedExtOfferSelectIndexes = this.props.getSelectedExtProcessSelectIndexes();
        if (!selectedExtOfferSelectIndexes) {
            return null;
        }
        let lastLevel = selectedExtOfferSelectIndexes.length - 1;
        let extProcessSelect = this.props.getExtProcessSelectForLevel(lastLevel);
        let selectedOption = extProcessSelect.options[selectedExtOfferSelectIndexes[lastLevel]];
        return ([
            this.createExtProcessSelects(selectedExtOfferSelectIndexes),
            this.createLoyaltySelect(selectedOption),
            this.createInstallmentSelect(selectedOption)
        ])
    };

    createExtProcessSelects(selectedExtOfferSelectIndexes) {
        return selectedExtOfferSelectIndexes.map((selectedExtOfferSelectIndex, level) => {
            let extProcessSelect = this.props.getExtProcessSelectForLevel(level);
            return (
                <div>
                    <div className={`l-col l-col-small-12 l-col-medium-4 l-col-6 u-spacing-l`}>
                        <OraFloatingLabelSelect
                            id={`extProcessSelectLevel-${level}`}
                            options={this.props.createOptionsForExtProcessSelect(extProcessSelect)}
                            onChange={this.props.selectProcessForExtProcessSelect(extProcessSelect)}
                            selected={selectedExtOfferSelectIndex}
                            className="opl-input--size-m"
                            label={extProcessSelect.label}
                            disabled={this.props.isDuet}/>
                    </div>
                    {this.isNotLastLevel(level, selectedExtOfferSelectIndexes) && <div className="l-col u-medium-hide"></div>}
                </div>);
        })
    };
    
    isNotLastLevel(level, selectedExtOfferSelectIndexes) {
        return level !== selectedExtOfferSelectIndexes.length - 1;
    }

    createLoyaltySelect(selectedOption) {
        if (selectedOption.showLoyalty) {
            return (<div className="l-col l-col-small-12 l-col-medium-4 l-col-6 u-spacing-l">
                <OraFloatingLabelSelect
                    id="loyaltyLengthFilter"
                    options={this.props.loyaltyLengthData}
                    selected={this.props.selectedLoyaltyLengthValue}
                    onChange={this.props.selectLoyaltyLengthCallback}
                    className="opl-input--size-m"
                    singleOptionClassName="u-spacing-l u-medium-inline-block u-medium-padding-left-xxl"
                    label="Umowa"
                    disabled={!!this.props.addTerminalToOfferBundleNo || this.props.isDuet}/>
            </div>);
        }
        return null;
    };

    createInstallmentSelect(selectedOption) {
        if (selectedOption.showInstallments) {
            return (<div className="l-col l-col-small-12 l-col-medium-4 l-col-6 u-spacing-l">
                <OraFloatingLabelSelect
                    id="deviceInstallmentsCountFilter"
                    options={this.props.installmentOptions}
                    selected={this.props.installmentCount}
                    className="opl-input--size-m"
                    singleOptionClassName="u-spacing-l u-medium-inline-block u-medium-padding-left-xxl"
                    label="Liczba rat za telefon lub urządzenie"
                    disabled={true}/>
            </div>);
        }
        return null;
    };

    isInstalmentProcess() {
        return this.props.selectedProcessValue === 'INSTALMENT_SALES_OF_GOODS' || this.props.selectedProcessValue === 'INSTALMENT_SALES_OF_GOODS_NC';
    }

    hasExtProcessSelectConfig() {
        return this.props.extProcessSelectConfig && this.props.extProcessSelectConfig.options
            && this.props.extProcessSelectConfig.options.length !== 0;
    }

    render() {
        let info = {
            label: this.props.descriptions[`infoDescription.${this.props.marketContext}.${this.props.selectedProcessValue}`],
            tooltip: this.props.descriptions[`infoTooltipDescription.${this.props.marketContext}.${this.props.selectedProcessValue}`]
        };
        let isSimfreeInst = this.props.selectedOfferType === "SIMFREE_INST";
        return (
            <div
                className={"l-col l-col-12 g-gray1-bg u-large-no-bg u-medium-padding-left-l u-medium-padding-right-l u-no-padding-bottom" + (isSimfreeInst ? ' u-spacing-l-xl' : '')}>
                <OraLoader id="offer-filters-loader-component" loading={this.props.offerFiltersLoading}
                           parentId="offer-filters-loader">
                <div
                    className={"g-gray1-bg u-medium-no-bg g-small-no-bg u-padding-all-l u-small-padding-left u-small-padding-right u-medium-no-padding-left u-medium-no-padding-right"
                    + (this.isInstalmentProcess() ? " u-no-padding-b" : "")}>
                    <div className="l-row ">
                        {!this.props.offerFiltersLoading && this.hasExtProcessSelectConfig() && this.createExtOfferSelect()}
                        {!this.props.offerFiltersLoading && !this.hasExtProcessSelectConfig() &&<div>
                            <div className="l-col l-col-small-12 l-col-medium-6 l-col-6  u-spacing-l">
                                <OraFloatingLabelSelect
                                    options={this.props.processTypeData}
                                    id="processTypeFilter"
                                    selected={this.props.selectedProcessValue}
                                    onChange={this.props.selectProcessCallback}
                                    isFeedbackParam="true"
                                    label={this.props.processLabel}
                                    className="opl-input--size-m"
                                    disabled={!!this.props.isAddTerminalToOfferBundleNo || this.props.isDuet}
                                />
                            </div>
                            {!isSimfreeInst && <div className="l-col l-col-small-12 l-col-medium-6 l-col-6  u-spacing-l">
                                <OraFloatingLabelSelect
                                    options={this.props.loyaltyLengthData}
                                    id="loyaltyFilter"
                                    selected={this.props.selectedLoyaltyLengthValue}
                                    onChange={this.props.selectLoyaltyLengthCallback}
                                    singleOptionClassName="u-padding-all"
                                    isFeedbackParam="true"
                                    label={"Umowa"}
                                    className="opl-input--size-m"
                                    disabled={!!this.props.isAddTerminalToOfferBundleNo || this.props.isDuet}
                                />
                            </div>}
                        </div>}
                        {!this.props.offerFiltersLoading && info.label &&
                        <div className="l-col l-col-12">
                            <InfoWithTooltip {...info}/>
                        </div>
                        }
                        {(!isSimfreeInst) && (this.props.channels.sales === "WWW") &&
                        <div className="l-col l-col-small-12 l-col-medium-12 l-col-12">
                            <ClientContextCheckbox key={this.props.selectedProcessValue}
                                                   channels={this.props.channels}

                                                       tooltip={true} {...this.props.clientContextCheckboxConfig}
                                                   addTerminalToOfferBundleNo={!!this.props.isAddTerminalToOfferBundleNo ? true : undefined}/>
                        </div>}

                    </div>


                    {this.props.selectedOffer &&
                    <div className={this.isInstalmentProcess() ? "" : "l-row u-padding-top-l"}>

                        {!isSimfreeInst && !this.props.isB2B
                        && [OfferType.DATA].indexOf(this.props.selectedOfferType) === -1
                        && <OfferDisclaimerComponent descriptions={this.props.descriptions} processType={this.props.selectedProcessValue} className="u-spacing-s"/>}

                        <MultiCartMnpBenefitComponent processType={this.props.selectedProcessValue}
                                                      monthlyPrices={this.props.selectedOffer.payments.plain.totalPayments.monthlyPayments}
                        />
                    </div>
                    }
                    {!isSimfreeInst && !this.props.isB2B &&
                    (
                        <div className="u-padding-top-m">
                            <DiscountInfo descriptions={this.props.descriptions} contractRole={this.props.contractRole}
                                          offerType={this.props.selectedOfferType}/>
                        </div>
                    )
                    }


                </div>
                </OraLoader>
            </div>
        )
    }
}

ProductDetailsProcessFilterView.propTypes = propTypes;
